import { useQuery } from "@apollo/client";
import { Affix, List } from "antd";
import { Content } from "antd/lib/layout/layout";
import Paragraph from "antd/lib/typography/Paragraph";
import Text from "antd/lib/typography/Text";
import Title from "antd/lib/typography/Title";

import React, { useState } from "react";
import { Link, RouteComponentProps } from "react-router-dom";
import { ErrorBanner, PageSkeleton } from "../../lib/components";
import { ListingCard } from "../../lib/components/ListingCard";
import { ListingFilter } from "../../lib/graphQl/globaltypes";
import {
  Listings as ListingsData,
  ListingsVariables,
  LISTINGS,
} from "../../lib/graphQl/queries";
import {
  ListingsFilters,
  ListingsPagination,
  ListingsSkeleton,
} from "./Components";

const PAGE_LIMIT = 8;

interface MatchParams {
  location?: string;
}

const Listings = (props: RouteComponentProps<MatchParams>) => {
  const [page, setPage] = React.useState(1);

  const [filter, setFilter] = React.useState<ListingFilter>(
    ListingFilter.PRICE_LOW_TO_HIGH
  );
  const locationRef = React.useRef(props.match.params.location);
  const { data, loading, error } = useQuery<ListingsData, ListingsVariables>(
    LISTINGS,
    {
      skip: locationRef.current !== props.match.params.location && page !== 1,
      variables: {
        location: props.match.params.location || null,
        filter,
        limit: PAGE_LIMIT,
        page,
      },
    }
  );

  React.useEffect(() => {
    setPage(1);
  }, [props.match.params.location]);

  const listings = data ? data.listings : null;
  const listingsRegion = listings?.region || null;
  if (loading) {
    return (
      <Content className="listings">
        <ListingsSkeleton />
      </Content>
    );
  }
  if (error) {
    return (
      <Content className="listings">
        <ErrorBanner message="We either couldn't find anything matching your search or have encountered an error.If you're searching for unique location, try searching again with more common keywords" />
        <ListingsSkeleton />
      </Content>
    );
  }
  const listingSectionElement =
    listings && listings.result.length ? (
      <div>
        <Affix offsetTop={64}>
          <ListingsPagination
            total={listings.total || 0}
            page={page}
            limit={PAGE_LIMIT}
            setPage={setPage}
          />
          <ListingsFilters filter={filter} setFilter={setFilter} />
        </Affix>
        <List
          dataSource={listings.result}
          grid={{
            gutter: 8,
            xs: 1,
            sm: 2,
            md: 4,
            lg: 4,
            xl: 4,
            xxl: 4,
          }}
          renderItem={(listing) => (
            <List.Item>
              <ListingCard listing={listing} />
            </List.Item>
          )}
        ></List>
      </div>
    ) : (
      <div>
        <Paragraph>
          It appears that no listings have yet been created for
          {<Text mark>"{listingsRegion}" </Text>}
        </Paragraph>
        <Paragraph>
          Be the first person to create a{" "}
          <Link to="/host">listing in this area</Link>
        </Paragraph>
      </div>
    );

  const listingsRegionElement = listingsRegion ? (
    <Title className="listings__title">Result for "{listingsRegion}"</Title>
  ) : null;

  return (
    <Content className="listings">
      {listingsRegionElement}
      {listingSectionElement}
    </Content>
  );
};

export { Listings };
