import { List } from "antd";
import Paragraph from "antd/lib/typography/Paragraph";
import Text from "antd/lib/typography/Text";
import Title from "antd/lib/typography/Title";
import { ListingCard } from "../../../../lib/components/ListingCard";
import { User } from "../../../../lib/graphQl/queries";

interface Props {
  userBookings?: User["user"]["bookings"];
  bookingsPage: number;
  limit: number;
  setBookingsPage: (page: number) => void;
}
export const UserBookings = ({
  limit,
  bookingsPage,
  setBookingsPage,
  userBookings,
}: Props) => {
  const { total, result } = userBookings || {
    total: undefined,
    result: undefined,
  };
  const userBookingsList = (
    <List
      grid={{
        gutter: 8,
        xs: 1,
        sm: 2,
        lg: 4,
        xl: 4,
        xxl: 4,
      }}
      dataSource={result}
      locale={{ emptyText: "User doesn't have any bookings yet!" }}
      pagination={{
        position: "top",
        current: bookingsPage,
        total,
        defaultPageSize: limit,
        hideOnSinglePage: true,
        showLessItems: true,
        onChange: (page: number) => setBookingsPage(page),
      }}
      renderItem={(userBooking) => {
        const bookingHistory = (
          <div className="listing-bookings__history">
            <div>
              Check in: <Text strong>{userBooking.checkIn}</Text>
            </div>
            <div>
              Check out: <Text strong>{userBooking.checkOut}</Text>
            </div>
          </div>
        );
        return (
          <List.Item>
            {bookingHistory}
            <ListingCard listing={userBooking.listings} />
          </List.Item>
        );
      }}
    />
  );
  return (
    <div className="user-listings">
      <Title level={4} className="user-listings__title">
        Bookings
      </Title>
      <Paragraph className="user-listings__description">
        This section highlights the listings this user currently hosts and has
        made available for bookings.
      </Paragraph>
      {userBookingsList}
    </div>
  );
};
