import React from "react";
import { Skeleton } from "antd";

const PageSkeleton = () => {
  const SkeletonParagraph = (
    <Skeleton active paragraph className="page-skeleton__paragraph" />
  );
  return (
    <>
      {SkeletonParagraph}
      {SkeletonParagraph}
      {SkeletonParagraph}
    </>
  );
};

export { PageSkeleton };
