import React from "react";
import { useApolloClient, useMutation } from "@apollo/client";
import { Card, Layout, Spin, Typography } from "antd";
import { LogIn, LogInVariables, LOG_IN } from "../../lib/graphQl/mutations";
import { AUTH_URL } from "../../lib/graphQl/queries";
import { AuthUrl } from "../../lib/graphQl/queries/AuthUrl/__generated__/AuthUrl";
import { Viewer } from "../../lib/types";
import googleLogo from "./assets/google_logo.jpg";
import {
  displayErrorMessage,
  displaySuccessNotification,
} from "../../lib/utils";
import { ErrorBanner } from "../../lib/components";
import { Redirect } from "react-router-dom";
const { Content } = Layout;
const { Text, Title } = Typography;

interface Props {
  setViewer: (viewer: Viewer) => void;
}

const Login = (props: Props) => {
  const client = useApolloClient();

  const [
    login,
    { loading: loginLoading, data, error: loginError },
  ] = useMutation<LogIn, LogInVariables>(LOG_IN, {
    onCompleted: (data) => {
      if (data && data.logIn) {
        props.setViewer(data.logIn);
        displaySuccessNotification(`You've successfully logged In`);
      }
    },
  });

  const logInRef = React.useRef(login);

  React.useEffect(() => {
    const code = new URL(window.location.href).searchParams.get("code");
    if (code) {
      logInRef.current({
        variables: {
          input: {
            code,
          },
        },
      });
    }
  }, []);

  const handleSignInWithGoogle = async () => {
    try {
      const { data } = await client.query<AuthUrl>({
        query: AUTH_URL,
      });
      window.location.href = data.authUrl;
    } catch (error) {
      displayErrorMessage(
        `Sorry! we weren't able to log you in.Please try again later!`
      );
    }
  };

  if (loginLoading) {
    return (
      <Content className="log-in">
        <Spin size="large" tip="Logging you in.." />
      </Content>
    );
  }

  if (data && data.logIn.id) {
    const { id } = data.logIn;
    return <Redirect to={`/user/${id}`} />;
  }
  const ErrorBannerElement = loginError ? (
    <ErrorBanner
      message={`Sorry! we weren't able to log you in.Please try again later!`}
    />
  ) : null;
  return (
    <Content className="log-in">
      {ErrorBannerElement}
      <Card className="log-in-card">
        <div className="log-in-card__intro">
          <Title level={3} className="log-in-card__intro-title">
            <span role="img" aria-label="wave">
              
            </span>
          </Title>
          <Title level={3} className="log-in-card__intro-title">
            Log in to TinyHouse!
          </Title>
          <Text>Sign in with Google to start booking available rentals!</Text>
        </div>
        <button
          className="log-in-card__google-button"
          onClick={handleSignInWithGoogle}
        >
          <img
            src={googleLogo}
            alt="Google Logo"
            className="log-in-card__google-button-logo"
          />
          <span className="log-in-card__google-button-text">
            Sign in with Google
          </span>
        </button>
        <Text type="secondary">
          Note: By signing in, you'll be redirected to the Google consent form
          to sign in with your Google account.
        </Text>
      </Card>
    </Content>
  );
};

export { Login };
