import React from "react";
import { Content } from "antd/lib/layout/layout";
import { Link, RouteComponentProps } from "react-router-dom";
import Title from "antd/lib/typography/Title";
import Paragraph from "antd/lib/typography/Paragraph";
import { Col, Row } from "antd";
import { HomeHero, HomeListings, HomeListingsSkeleton } from "./components";
import cancunImage from "./assets/cancun.jpg";
import mapBackground from "./assets/map-background.jpg";
import sanFransiscoImage from "./assets/san-fransisco.jpg";
import { displayErrorMessage } from "../../lib/utils";
import { useQuery } from "@apollo/client";
import {
  Listings,
  LISTINGS,
  ListingsVariables,
} from "../../lib/graphQl/queries";
import { ListingFilter } from "../../lib/graphQl/globaltypes";

const PAGE_LIMIT = 4;

const Home = (props: RouteComponentProps) => {
  const { data, loading, error } = useQuery<Listings, ListingsVariables>(
    LISTINGS,
    {
      variables: {
        filter: ListingFilter.PRICE_HIGH_TO_LOW,
        limit: PAGE_LIMIT,
        page: 1,
      },
    }
  );
  const onSearch = (val: string) => {
    const trimmedVal = val.trim();
    if (trimmedVal) {
      props.history.push(`/listings/${trimmedVal}`);
    } else {
      displayErrorMessage("Please enter a valid search");
    }
  };

  const renderListingSection = () => {
    if (loading) {
      return <HomeListingsSkeleton />;
    }
    if (data) {
      return (
        <HomeListings
          listings={data.listings.result}
          title="Premium Listings"
        />
      );
    }
    return null;
  };

  return (
    <Content
      className="home"
      style={{ backgroundImage: `url(${mapBackground})` }}
    >
      <HomeHero onSearch={onSearch} />
      <div className="home__cta-section">
        <Title level={2} className="home__cta-section-title">
          Your guide for all things rental
        </Title>
        <Paragraph>
          Helping you make the best decisions in renting your last minute
          locations.
        </Paragraph>
        <Link
          to="/listings/united%20states"
          className="ant-btn ant-btn-primary ant-btn-lg home__cta-section-button"
        >
          Popular listings in the United States
        </Link>
      </div>
      {renderListingSection()}
      <div className="home__listings">
        <Title level={4} className="home__listings-title">
          Listings of any kind
        </Title>
        <Row gutter={12}>
          <Col xs={24} sm={12}>
            <Link to="/listings/san%20fransisco">
              <div className="home__listings-img-cover">
                <img
                  src={sanFransiscoImage}
                  alt="San Fransisco"
                  className="home__listings-img"
                />
              </div>
            </Link>
          </Col>
          <Col xs={24} sm={12}>
            <Link to="/listings/cancn">
              <div className="home__listings-img-cover">
                <img
                  src={cancunImage}
                  alt="Cancn"
                  className="home__listings-img"
                />
              </div>
            </Link>
          </Col>
        </Row>
      </div>
    </Content>
  );
};

export { Home };
