import React from "react";

// import {
//   CardElement,
//   injectStripe,
//   ReactStripeElements,
// } from "react-stripe-elements";
import { Button, Divider, Modal, Typography } from "antd";
import moment, { Moment } from "moment";
// import { CREATE_BOOKING } from "../../../../lib/graphql/mutations";
// import {
//   CreateBooking as CreateBookingData,
//   CreateBookingVariables,
// } from "../../../../lib/graphql/mutations/CreateBooking/__generated__/CreateBooking";
import {
  formatPrice,
  displaySuccessNotification,
  displayErrorMessage,
} from "../../../../lib/utils";
import { useMutation } from "@apollo/client";
import { CardElement, useElements, useStripe } from "@stripe/react-stripe-js";
import { KeyOutlined } from "@ant-design/icons";
import { Viewer } from "../../../../lib/types";
import { PaymentMethod, StripeError, Token } from "@stripe/stripe-js";
import {
  CreateBooking,
  CreateBookingVariables,
} from "../../../../lib/graphQl/mutations/CreateBooking/__generated__/CreateBooking";
import { CREATE_BOOKING } from "../../../../lib/graphQl/mutations";

interface Props {
  id: string;
  price: number;
  modalVisible: boolean;
  checkInDate: Moment;
  checkOutDate: Moment;
  setModalVisible: (modalVisible: boolean) => void;
  clearBookingData: () => void;
  handleListingRefetch: () => Promise<void>;
}

const { Paragraph, Text, Title } = Typography;

interface CheckOutFormProps {
  onSuccess: (token: Token) => void;
  onError: (error: StripeError) => void;
  loading?: boolean;
}
const CheckOutForm = ({
  onError,
  onSuccess,
  loading = false,
}: CheckOutFormProps) => {
  const stripe = useStripe();
  const elements = useElements();

  const handleSubmit = async (event: React.FormEvent<HTMLFormElement>) => {
    // We don't want to let default form submission happen here,
    // which would refresh the page.
    event.preventDefault();
    if (!stripe || !elements) {
      // Stripe.js has not yet loaded.
      // Make sure to disable form submission until Stripe.js has loaded.
      return;
    }
    const cardElement = elements.getElement(CardElement);
    if (!cardElement) {
      return;
    }

    const result = await stripe.createToken(cardElement);

    if (result.error) {
      onError(result.error);
    } else {
      if (result.token) {
        onSuccess(result.token);
      }
    }
  };

  return (
    <form onSubmit={handleSubmit}>
      <CardElement
        className="listing-booking-modal__stripe-card"
        options={{ hidePostalCode: true }}
      />

      <Button
        size="large"
        htmlType="submit"
        type="primary"
        className="listing-booking-modal__cta"
        loading={loading}
      >
        Book
      </Button>
    </form>
  );
};

export const ListingCreateBookingModal = ({
  id,
  price,
  modalVisible,
  checkInDate,
  checkOutDate,
  setModalVisible,
  clearBookingData,
  handleListingRefetch,
}: Props) => {
  const [createBooking, { loading }] = useMutation<
    CreateBooking,
    CreateBookingVariables
  >(CREATE_BOOKING, {
    onCompleted: () => {
      clearBookingData();
      displaySuccessNotification(
        "You've successfully booked the listing!",
        "Booking history can always be found in your User page."
      );
      handleListingRefetch();
    },
    onError: () => {
      displayErrorMessage(
        "Sorry! We weren't able to successfully book the listing. Please try again later!"
      );
    },
  });

  const daysBooked = checkOutDate.diff(checkInDate, "days") + 1;
  const listingPrice = price * daysBooked;

  return (
    <Modal
      visible={modalVisible}
      centered
      footer={null}
      onCancel={() => setModalVisible(false)}
    >
      <div className="listing-booking-modal">
        <div className="listing-booking-modal__intro">
          <Title className="listing-boooking-modal__intro-title">
            <KeyOutlined />
          </Title>
          <Title level={3} className="listing-boooking-modal__intro-title">
            Book your trip
          </Title>
          <Paragraph>
            Enter your payment information to book the listing from the dates
            between{" "}
            <Text mark strong>
              {moment(checkInDate).format("MMMM Do YYYY")}
            </Text>{" "}
            and{" "}
            <Text mark strong>
              {moment(checkOutDate).format("MMMM Do YYYY")}
            </Text>
            , inclusive.
          </Paragraph>
        </div>

        <Divider />

        <div className="listing-booking-modal__charge-summary">
          <Paragraph>
            {formatPrice(price, false)} * {daysBooked} days ={" "}
            <Text strong>{formatPrice(listingPrice, false)}</Text>
          </Paragraph>
          <Paragraph className="listing-booking-modal__charge-summary-total">
            Total = <Text mark>{formatPrice(listingPrice, false)}</Text>
          </Paragraph>
        </div>

        <Divider />

        <div className="listing-booking-modal__stripe-card-section">
          <CheckOutForm
            loading={loading}
            onError={(err) => {
              if (err.message) {
                displayErrorMessage(err.message);
              } else {
                displayErrorMessage(
                  "Sorry! We weren't able to connect with Stripe."
                );
              }
            }}
            onSuccess={(token) => {
              createBooking({
                variables: {
                  input: {
                    id,
                    source: token.id,
                    checkIn: moment(checkInDate).format("YYYY-MM-DD"),
                    checkOut: moment(checkOutDate).format("YYYY-MM-DD"),
                  },
                },
              });
              handleListingRefetch().then(() => {
                setModalVisible(false);
              });
            }}
          />
        </div>
      </div>
    </Modal>
  );
};
