import { useQuery } from "@apollo/client";
import React from "react";

import { Col, Row } from "antd";
import { Content } from "antd/lib/layout/layout";

import { RouteComponentProps } from "react-router-dom";
import { ErrorBanner, PageSkeleton } from "../../lib/components";
import {
  USER,
  User as UserData,
  UserVariables,
} from "../../lib/graphQl/queries";
import { Viewer } from "../../lib/types";
import { UserBookings, UserListings, UserProfile } from "./components";

const PAGE_LIMIT = 4;

interface MatchParams {
  id: string;
}
interface Props {
  viewer: Viewer;
  setViewer: (viewer: Viewer) => void;
}

const User = (props: Props & RouteComponentProps<MatchParams>) => {
  const [bookingsPage, setBookingsPage] = React.useState(1);
  const [listingsPage, setListingsPage] = React.useState(1);
  const isOwner = props.viewer.id === props.match.params.id;
  const { loading, data, error, refetch } = useQuery<UserData, UserVariables>(
    USER,
    {
      variables: {
        id: props.match.params.id,
        limit: PAGE_LIMIT,
        bookingsPage,
        listingsPage,
      },
    }
  );

  const bookingsElement = isOwner ? (
    <UserBookings
      userBookings={data?.user.bookings}
      bookingsPage={bookingsPage}
      limit={PAGE_LIMIT}
      setBookingsPage={setBookingsPage}
    />
  ) : null;
  const listingsElement = (
    <UserListings
      userListings={data?.user.listings}
      listingsPage={listingsPage}
      limit={PAGE_LIMIT}
      setListingsPage={setListingsPage}
    />
  );

  if (loading) {
    return (
      <Content className="user">
        <PageSkeleton />
      </Content>
    );
  }
  if (error) {
    return (
      <Content className="user">
        <ErrorBanner
          description={`This is user may not exist or we've encountered an error.please try again later`}
        />
        <PageSkeleton />
      </Content>
    );
  }
  const UserProfileElement = data?.user ? (
    <UserProfile
      user={data.user}
      isOwner={isOwner}
      viewer={props.viewer}
      setViewer={props.setViewer}
      handleUserRefecth={refetch}
    />
  ) : null;
  return (
    <Content className="user">
      <Row gutter={12} justify="space-between">
        <Col xs={24}>{UserProfileElement}</Col>
      </Row>
      {bookingsElement}
      {listingsElement}
    </Content>
  );
};

export { User };
