import {
  ApolloClient,
  ApolloLink,
  ApolloProvider,
  HttpLink,
  InMemoryCache,
  useMutation,
  concat,
} from "@apollo/client";
import { Elements, ElementsConsumer } from "@stripe/react-stripe-js";
import { loadStripe } from "@stripe/stripe-js";
import { Affix, Spin } from "antd";
import Layout from "antd/lib/layout/layout";
import React from "react";
import { render } from "react-dom";
import { BrowserRouter, Route, Switch } from "react-router-dom";
import { ErrorBanner } from "./lib/components";
import { LogIn, LogInVariables, LOG_IN } from "./lib/graphQl/mutations";
import { Viewer } from "./lib/types";
import {
  AppHeader,
  AppHeaderSkeleton,
  Home,
  Host,
  Listing,
  Listings,
  NotFound,
  Stripe,
  User,
} from "./section";
import { Login } from "./section/Login";
import "./styles/index.css";

const httpLink = new HttpLink({ uri: "/api" });

const authMiddleware = new ApolloLink((operation, forward) => {
  // add the authorization to the headers
  const token = sessionStorage.getItem("token");
  operation.setContext({
    headers: {
      "x-csrf-token": token || "",
    },
  });
  return forward(operation);
});

const client = new ApolloClient({
  link: concat(authMiddleware, httpLink),
  cache: new InMemoryCache(),
  connectToDevTools: true,
});

const intialViewer: Viewer = {
  id: null,
  avatar: null,
  didRequest: false,
  token: null,
  hasWallet: null,
};

const StripePromise = loadStripe(
  process.env.REACT_APP_STRIPE_PUBLIC_KEY as string
);

const App = () => {
  const [viewer, setViewer] = React.useState<Viewer>(intialViewer);
  const [logIn, { error }] = useMutation<LogIn, LogInVariables>(LOG_IN, {
    onCompleted: (data) => {
      if (data && data.logIn) {
        setViewer(data.logIn);
        if (data.logIn.token) {
          sessionStorage.setItem("token", data.logIn.token);
        } else {
          sessionStorage.removeItem("token");
        }
      }
    },
  });

  const logInRef = React.useRef(logIn);
  React.useEffect(() => {
    logInRef.current();
  }, []);

  if (!viewer.didRequest) {
    return (
      <Layout className="app-skeleton">
        <AppHeaderSkeleton />
        <div className="app-skeleton__spin-section">
          <Spin size="large" tip="Launching Tinyhouse" />
        </div>
      </Layout>
    );
  }

  const logInErrorBanner = error ? (
    <ErrorBanner
      message={`We weren't able to verify if you were logged in .Please try agin later`}
    />
  ) : null;

  return (
    <Layout id="app">
      {logInErrorBanner}
      <Elements stripe={StripePromise}>
        <BrowserRouter>
          <Affix offsetTop={0} className="app__affix-header">
            <AppHeader viewer={viewer} setViewer={setViewer} />
          </Affix>
          <Switch>
            <Route exact path="/" component={Home} />
            <Route
              exact
              path="/host"
              render={(props) => <Host viewer={viewer} {...props} />}
            />
            <Route
              exact
              path="/login"
              render={(props) => <Login {...props} setViewer={setViewer} />}
            />
            <Route
              exact
              path="/listing/:id"
              render={(props) => <Listing {...props} viewer={viewer} />}
            />
            <Route exact path="/listings/:location?" component={Listings} />
            <Route
              exact
              path="/user/:id"
              render={(props) => (
                <User {...props} viewer={viewer} setViewer={setViewer} />
              )}
            />
            <Route
              exact
              path="/stripe"
              render={(props) => (
                <Stripe {...props} viewer={viewer} setViewer={setViewer} />
              )}
            />
            <Route component={NotFound} />
          </Switch>
        </BrowserRouter>
      </Elements>
    </Layout>
  );
};

render(
  <ApolloProvider client={client}>
    <App />
  </ApolloProvider>,
  document.querySelector("#root")
);
