import React from "react";
import { useQuery } from "@apollo/client";
import {
  Listing as ListingData,
  LISTING,
  ListingVariables,
} from "../../lib/graphQl/queries";
import { RouteComponentProps } from "react-router-dom";
import { ErrorBanner, PageSkeleton } from "../../lib/components";
import { Content } from "antd/lib/layout/layout";
import { ListingDetails } from "./components/ListingDetails";
import { Col, Row } from "antd";
import { ListingBookings } from "./components/ListingBookings";
import { ListingCreateBooking } from "./components/ListingCreateBooking";
import { Moment } from "moment";
import { Viewer } from "../../lib/types";
import { ElementsConsumer } from "@stripe/react-stripe-js";
import { ListingCreateBookingModal } from "./components/ListingCreateBookingModal";

interface MatchParams {
  id: string;
}
interface Props {
  viewer: Viewer;
}
const PAGE_LIMIT = 4;

const Listing = ({
  match,
  viewer,
}: Props & RouteComponentProps<MatchParams>) => {
  const [bookingPage, setBookingPage] = React.useState(1);
  const [isModalVisible, setIsModalVisible] = React.useState(false);
  const [checkInDate, setcheckInDate] = React.useState<Moment | null>(null);
  const [checkOutDate, setcheckOutDate] = React.useState<Moment | null>(null);
  const { data, loading, error, refetch } = useQuery<
    ListingData,
    ListingVariables
  >(LISTING, {
    variables: {
      bookingsPage: 1,
      id: match.params.id,
      limit: PAGE_LIMIT,
    },
  });
  if (loading) {
    return (
      <Content className="listing">
        <PageSkeleton />
      </Content>
    );
  }
  if (error) {
    return (
      <Content className="listing">
        <ErrorBanner description="This listing may not exist or we've encounterd an error.Please try again soon!" />
        <PageSkeleton />
      </Content>
    );
  }
  const listing = data ? data.listing : null;
  const listingBooking = listing ? listing.bookings : null;
  const listingBookingElement = listingBooking ? (
    <ListingBookings
      bookingsPage={bookingPage}
      limit={PAGE_LIMIT}
      listingBookings={listingBooking}
      setBookingsPage={setBookingPage}
    />
  ) : null;

  const listingCreateBookingElement = listing ? (
    <ListingCreateBooking
      bookingsIndex={listing.bookingsIndex || "{}"}
      checkInDate={checkInDate}
      checkOutDate={checkOutDate}
      host={listing.host}
      price={listing.price}
      setCheckInDate={setcheckInDate}
      setCheckOutDate={setcheckOutDate}
      setModalVisible={setIsModalVisible}
      viewer={viewer}
    />
  ) : null;

  return (
    <Content className="listing">
      {listing?.price && checkInDate && checkOutDate && (
        <ListingCreateBookingModal
          checkInDate={checkInDate}
          checkOutDate={checkOutDate}
          clearBookingData={() => {}}
          handleListingRefetch={async () => {
            await refetch();
          }}
          modalVisible={isModalVisible}
          price={listing.price}
          setModalVisible={setIsModalVisible}
          id={listing.id}
        />
      )}

      <Row gutter={24} justify="space-between">
        <Col xs={24} lg={14}>
          <ListingDetails listing={listing} />
          {listingBookingElement}
        </Col>
        <Col xs={24} lg={10}>
          {listingCreateBookingElement}
        </Col>
      </Row>
    </Content>
  );
};

export { Listing };
