import { message, notification } from "antd";

const PRIMARY_COLOR = "#1890ff";

const formatPrice = (price: number, round = true): string => {
  const formatedPrice = "$" + (round ? Math.round(price / 100) : price / 100);
  return formatedPrice;
};

const displaySuccessNotification = (message: string, description?: string) => {
  return notification.success({
    message,
    description,
    placement: "topLeft",
    style: {
      marginTop: 50,
    },
  });
};

const displayErrorMessage = (error: string) => {
  return message.error(error);
};

export {
  displaySuccessNotification,
  displayErrorMessage,
  formatPrice,
  PRIMARY_COLOR,
};
