import { useMutation } from "@apollo/client";
import userEvent from "@testing-library/user-event";
import { Button, Card, Divider, Tag } from "antd";
import Avatar from "antd/lib/avatar/avatar";
import Paragraph from "antd/lib/typography/Paragraph";
import Text from "antd/lib/typography/Text";
import Title from "antd/lib/typography/Title";

import React from "react";
import { ErrorBanner } from "../../../../lib/components";
import { DISCONNECT_STRIPE } from "../../../../lib/graphQl/mutations";
import { DisconnectStripe } from "../../../../lib/graphQl/mutations/DisconnectStripe/__generated__/DisconnectStripe";
import { User } from "../../../../lib/graphQl/queries";
import { Viewer } from "../../../../lib/types";
import {
  displayErrorMessage,
  displaySuccessNotification,
  formatPrice,
} from "../../../../lib/utils";

interface Props {
  user: User["user"];
  isOwner: boolean;
  viewer: Viewer;
  setViewer: (viewer: Viewer) => void;
  handleUserRefecth: () => void;
}

const stripeUrl = `https://connect.stripe.com/oauth/authorize?response_type=code&client_id=${process.env.REACT_APP_STRIPE_CLIENT_ID}&scope=read_write&redirect_uri=http://localhost:3000/stripe`;

const UserProfile = (props: Props) => {
  const [disconnectStripe, { data, loading }] = useMutation<DisconnectStripe>(
    DISCONNECT_STRIPE,
    {
      onCompleted: (data) => {
        if (data && data.disconnectStripe) {
          props.setViewer({ ...props.viewer, hasWallet: null });
          displaySuccessNotification(
            "You've successfully disconnected from Stripe",
            "You'll have to reconnect with stripe to continue to create listing."
          );
          props.handleUserRefecth();
        }
      },
      onError: () => {
        displayErrorMessage(
          "Sorry! We weren't able to disconnect from Stripe. Please try again later!"
        );
      },
    }
  );

  const redirectToStripe = () => {
    window.location.href = stripeUrl;
  };

  const stripeError = new URL(window.location.href).searchParams.get(
    "stripe_error"
  );
  const stripeErrorElement = stripeError ? (
    <ErrorBanner message="We had issue connecting with Stripe. Please try again later" />
  ) : null;

  const AdditionDetails = props.user.hasWallet ? (
    <>
      <Paragraph>
        <Tag color="green">Stripe Registered</Tag>
      </Paragraph>
      <Paragraph>
        Income Earned: <Text strong>{props.user.income}</Text>
      </Paragraph>
      <Button
        type="primary"
        className="user-profile__details-cta"
        onClick={() => disconnectStripe()}
      >
        Disconnect Stripe
      </Button>
      <Paragraph type="secondary">
        By disconnecting, you won't be able to receive{" "}
        <Text strong>any further payments</Text>. This will prevent users from
        booking listings that you might have already created.
      </Paragraph>
    </>
  ) : (
    <>
      <Paragraph>
        Interested in becoming a TinyHouse host? Register with your stripe
        account!
      </Paragraph>
      <Paragraph>
        Income Earned: <Text strong>{formatPrice(props.user.income || 0)}</Text>
      </Paragraph>
      <Button
        type="primary"
        className="user-profile__details-cta"
        onClick={redirectToStripe}
      >
        Connect With Stripe
      </Button>
      <Paragraph type="secondary">
        TinyHouse uses{" "}
        <a
          href="https://stripe.com/en-US/connect"
          target="_blank"
          rel="noopener noreferrer"
        >
          Stripe
        </a>
        to help transfer your earnings in a secure and truster manner.
      </Paragraph>
    </>
  );

  const AdditionalInformation = props.isOwner ? (
    <>
      <Divider />
      <Title level={4}>Addition Details</Title>
      {AdditionDetails}
    </>
  ) : null;
  return (
    <div className="user-profile">
      {stripeErrorElement}
      <Card className="user-profile__card">
        <div className="user-profile__avatar">
          <Avatar size={100} src={props.user.avatar} />
        </div>
        <Divider />
        <div className="user-profile__details">
          <Title level={4}>Details</Title>
          <Paragraph>
            Name: <Text strong>{props.user.name} </Text>
          </Paragraph>
          <Paragraph>
            Contact: <Text strong>{props.user.contact} </Text>
          </Paragraph>
          {AdditionalInformation}
        </div>
      </Card>
      {}
    </div>
  );
};

export { UserProfile };
